html, body {
  width: 100%;
  height: 100%;
}

body {
  padding-top: 60px;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

h1 {
  font-size: 1.25em;
  font-weight: 300;
  text-align: left;
  margin-bottom: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.model-viewer {
  height: calc(100vh - 240px);
}

.header {
  position: absolute;
  top: 0;
}

.artist-label {
  font-style: italic;
  font-size: 1em;
  font-weight: 400;
  color: #888;
}

.toolbar {
  height: 60px;
  position: absolute;
  bottom:0;
}

.btn-round {
  border-radius: 20px;
}

.init-page {
  height: calc(100vh - 300px);
}